<template>
  <div class="field__item">
    <div class="row">
      <span class="prop">{{field.prop}}</span>
      <span class="type">{{field.type || 'string'}}</span>
      <span class="required"
            v-if="!getRequired">选填</span>
      <span class="name">{{field.name || '无'}}</span>
    </div>
    <complex-type :field="field"
                  v-if="isComplexType || 'enum' in field" />
  </div>
</template>

<script>
import ComplexType from './index.vue';

export default {
  name: 'field',
  components: {
    ComplexType,
  },
  props: {
    field: Object,
  },
  computed: {
    isComplexType() {
      return ['array', 'enum', 'object'].includes(this.field.type);
    },
    getRequired() {
      return 'required' in this.field ? this.field.required : true;
    },
  },
};
</script>

<style lang="scss" scoped>
.row {
  margin: 10px 0;

  .type {
    color: #aaa;
    margin-left: 10px;
  }

  .required {
    border-radius: 2px 4px;
    border: 1px solid #ccc;
    margin-left: 10px;
    color: #444;
    font-size: 0.9rem;
    padding: 2px;
  }

  .name {
    margin-left: 10px;
  }
}
</style>
