import axios from 'axios';
import qs from 'qs';

const defaultHeaders = {
  'Content-Type': 'application/json',
};

export function getUrl() {
  const qsUrl = qs.parse(window.location.search.replace('?', ''));
  const baseURL = qsUrl.server_url ? qsUrl.server_url : 'http://139.155.127.14:9000'; // 'http://localhost:9000';
  return baseURL;
}


export default function createAPI({
  baseURL = getUrl(),
  url,
  method,
  headers = defaultHeaders,
  body = {},
}) {
  const m = method ? method.toLowerCase() : 'get';
  const instance = axios.create({ baseURL, headers });
  if (m === 'get') {
    const query = qs.stringify(body);
    return instance.get(`${url}${query ? '?' : ''}${query}`);
  }
  return instance[m](url, body);
}
