<template>
  <div class="complex__type">
    <Enum v-if="'enum' in field"
          :enums="field.enum" />
    <ObjectType v-if="isObject"
                title="子属性"
                :fields="field.object" />
    <ObjectType v-if="isArray"
                title="数组成员"
                :fields="field.item" />
  </div>
</template>

<script>
import Enum from './Enum.vue';
import ObjectType from './ObjectType.vue';

export default {
  name: 'ComplexType',
  components: {
    Enum,
    ObjectType,
  },
  props: {
    field: Object,
  },
  computed: {
    isObject() {
      return this.field.type === 'object';
    },
    isArray() {
      return this.field.type === 'array';
    },
  },
};
</script>

<style lang="scss" scoped>
.complex__type {
  color: #444;
  border-radius: 5px;
  border: 1px solid #ddd;
}
</style>
